@font-face {
  font-family: "Montserrat";
  src: url("../fonts/Montserrat-Thin.woff") format(woff), url("../fonts/Montserrat-Thin.woff2") format(woff2);
  font-weight: 100;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  src: url("../fonts/Montserrat/Montserrat-light.woff") format(woff), url("../fonts/Montserrat/Montserrat-light.woff2") format(woff2);
  font-weight: 300;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  src: url("../fonts/Montserrat-Regular.woff") format(woff), url("../fonts/Montserrat-Regular.woff2") format(woff2);
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  src: url("../fonts/Montserrat-Medium.woff") format(woff), url("../fonts/Montserrat-Medium.woff2") format(woff2);
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  src: url("../fonts/Montserrat-Bold.woff") format(woff), url("../fonts/Montserrat-Bold.woff2") format(woff2);
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
html, body, div, span, object, iframe, h1, h2, h3, h4, h5, h6, p, a, em, img, b, u, i, ol, ul, li, form, label, table, tbody, tfoot, thead, tr, th, td, input, textarea, button {
  margin: 0;
  padding: 0;
  border: none medium;
}

html, body {
  font-size: 16px;
  line-height: 1.5;
}

body {
  font-family: "Montserrat", Arial, sans-serif;
  background-color: #202124;
}
body.is-hidden {
  overflow: hidden;
}
@media (max-width: 1247px) {
  body {
    background-color: #131313;
  }
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

*, *:focus, *:after, *:before {
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  outline: none;
}

a, a:active {
  text-decoration: none;
}

img {
  max-width: 100%;
}

.wrapper {
  max-width: 1820px;
  width: 100%;
  padding: 0 50px;
  margin: 0 auto;
}
@media (max-width: 575px) {
  .wrapper {
    padding: 0 15px;
  }
}

button {
  display: inline-block;
  background: none;
  outline: none;
  cursor: pointer;
  font-size: 12px;
  font-weight: 700;
}

.skew {
  transform: skew(-15deg);
}
.skew__inner {
  transform: skew(15deg);
  display: block;
}

.btn {
  background-color: #C2AB81;
  padding: 0 75px;
  min-height: 60px;
  color: #ffffff;
  position: relative;
  white-space: nowrap;
}
@media (max-width: 767px) {
  .btn {
    min-height: 45px;
    padding: 0 47px;
  }
}
.btn::after {
  content: "";
  display: block;
  position: absolute;
  right: -8px;
  top: 0;
  bottom: 0;
  width: 4px;
  height: 100%;
  background-color: #ffffff;
  z-index: 10;
}

.title {
  font-size: 42px;
  font-weight: 700;
  line-height: 1em;
  text-transform: uppercase;
}
@media (max-width: 1023px) {
  .title {
    font-size: 38px;
  }
}
@media (max-width: 767px) {
  .title {
    font-size: 20px;
  }
}

.site-container {
  position: relative;
}

.btn-form:disabled {
  cursor: default;
  opacity: 0.5;
}

.field-error {
  border: solid 1px red !important;
}

.modal {
  display: none;
}
.modal.is-open {
  display: block;
}
.modal__inner {
  width: 50vw;
  z-index: 200;
  color: #ffffff;
  position: fixed;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  box-shadow: 0 0 10px rgba(255, 255, 255, 0.2);
  border-radius: 10px;
  padding: 40px;
  background-color: #979797;
}
@media (max-width: 767px) {
  .modal__inner {
    width: 80vw;
  }
}
.modal__close {
  position: absolute;
  right: 10px;
  top: 10px;
  display: block;
  width: 20px;
  height: 20px;
  cursor: pointer;
  z-index: 121;
  transition: 0.3s;
}
.modal__close:hover {
  transform: rotate(90deg);
}
.modal__close::after {
  content: "";
  width: 100%;
  height: 1px;
  background-color: #ffffff;
  display: block;
  transform: rotate(45deg);
  position: absolute;
  left: 0;
  top: 9px;
}
.modal__close::before {
  content: "";
  width: 100%;
  height: 1px;
  background-color: #ffffff;
  display: block;
  transform: rotate(-45deg);
  position: absolute;
  left: 0;
  top: 9px;
}

.modal-form input {
  display: block;
  width: 100%;
  padding: 10px 10px;
  outline: none;
  border: solid 1px transparent;
  margin: 0 0 15px;
  font-size: 14px;
}

.overlay {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.2);
  z-index: 150;
  backdrop-filter: blur(4px);
}

.overlay-menu {
  position: fixed;
  top: 100px;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: #131313;
  z-index: 100;
}

.about {
  background-image: url("../img/main-bg.webp");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: 50% 50%;
  color: #ffffff;
  display: flex;
  border-radius: 40px 40px 0 0;
  background-color: #3F3F3F;
  padding-bottom: 120px;
  position: relative;
}
@media (max-width: 767px) {
  .about {
    padding-bottom: 50px;
  }
}
.about::after {
  content: "";
  height: 30vw;
  width: 100%;
  display: block;
  position: absolute;
  bottom: 0;
  right: 0;
  left: 0;
  background-image: linear-gradient(to top, #202124 -60%, rgba(0, 0, 0, 0.2));
}
.about__inner {
  margin-top: auto;
  height: 100%;
  position: relative;
  z-index: 5;
}
.about__title {
  margin-bottom: 40px;
  max-width: 40%;
  font-family: "Montserrat", Arial, sans-serif;
  margin-top: 30vw;
}
@media (max-width: 1247px) {
  .about__title {
    max-width: 80%;
  }
}
@media (max-width: 767px) {
  .about__title {
    margin-bottom: 20px;
    margin-top: 265px;
  }
}
.about__btn {
  margin-left: 8px;
}
.about__text {
  font-size: 16px;
  font-weight: 400;
  line-height: 150%;
  margin-bottom: 40px;
  max-width: 45%;
  font-family: "Montserrat", Arial, sans-serif;
}
@media (max-width: 1247px) {
  .about__text {
    max-width: 60%;
  }
}
@media (max-width: 767px) {
  .about__text {
    max-width: 100%;
    margin-bottom: 20px;
  }
}
@media (max-width: 575px) {
  .about__text {
    font-size: 14px;
  }
}

.offer {
  padding-top: 135px;
  padding-bottom: 230px;
  padding: 135px 0 135px;
}
@media (max-width: 767px) {
  .offer {
    padding: 50px 0;
  }
}
.offer__title {
  color: #ffffff;
  padding-bottom: 50px;
}
.offer__list {
  display: flex;
  flex-wrap: wrap;
  align-items: flex-start;
  margin: 0 -15px;
  height: 100%;
}
.offer__more {
  padding: 30px auto 0;
  text-align: center;
}
.offer__more-btn.disabled {
  background-color: #dedede;
  cursor: default;
}

.offer-item {
  flex: 0 0 20%;
  max-width: 20%;
  padding: 0 15px 30px;
  align-self: stretch;
}
@media (max-width: 1440px) {
  .offer-item {
    flex: 0 0 33.333%;
    max-width: 33.333%;
  }
}
@media (max-width: 1247px) {
  .offer-item {
    flex: 0 0 50%;
    max-width: 50%;
  }
}
@media (max-width: 575px) {
  .offer-item {
    flex: 0 0 100%;
    max-width: 100%;
  }
}
.offer-item__inner {
  background-color: #ffffff;
  height: 100%;
  display: flex;
  flex-direction: column;
}
.offer-item__img img {
  width: 100%;
}
.offer-item__info {
  padding: 25px;
  height: 100%;
  display: flex;
  flex-direction: column;
}
.offer-item__name {
  text-transform: uppercase;
  font-size: 14px;
  font-weight: 700;
  margin-bottom: 20px;
}
.offer-item__title {
  font-size: 16px;
  line-height: 26px;
  margin-bottom: 20px;
  color: #171717;
}
.offer-item__text {
  font-size: 14px;
  line-height: 26px;
  color: #666666;
  margin-bottom: 20px;
}
.offer-item__post {
  font-size: 12px;
  color: #666666;
  margin-bottom: 25px;
}
.offer-item__btn {
  margin-top: auto;
  display: block;
}
.offer-item__btn button {
  background-color: #171717;
  text-align: center;
  color: #ffffff;
  font-size: 14px;
  display: block;
  width: 100%;
  font-weight: 500;
  min-height: 50px;
  transition: all 0.5s ease;
  transition: 0.4s;
}
.offer-item__btn button:hover {
  transition: all 0.5s ease;
  box-shadow: inset 0 0 0 30px #C2AB81;
}

.header {
  background-color: #202124;
}
@media (max-width: 1247px) {
  .header {
    background-color: #131313;
  }
}

.header-strip {
  display: flex;
  align-items: center;
  justify-content: space-between;
  color: #ffffff;
  position: relative;
  padding: 30px 0;
}
.header-strip__left {
  display: flex;
  align-items: center;
}
@media (max-width: 767px) {
  .header-strip__left {
    justify-content: space-between;
    width: 100%;
  }
}
.header-strip__right {
  display: flex;
  align-items: center;
  margin: 0 10px 0 0;
}
@media (max-width: 767px) {
  .header-strip__right {
    display: none;
  }
}
@media (max-width: 767px) {
  .header-strip__right.open-menu {
    display: flex;
    position: absolute;
    top: 420px;
    left: 0;
    z-index: 120;
    display: flex;
    flex-direction: column;
    width: 100%;
  }
}
@media (max-width: 767px) {
  .header-strip__right button {
    display: block;
    width: calc(100% - 30px);
    min-height: 60px;
  }
}
@media (max-width: 767px) {
  .header-strip__menu {
    position: absolute;
  }
}
.header-strip__menu.overlay-menu {
  position: fixed;
}
.header-strip__logo {
  margin-right: 75px;
  display: block;
  transition: 0.3s;
  width: 157px;
}
@media (max-width: 1440px) {
  .header-strip__logo {
    margin-right: 25px;
  }
}
@media (max-width: 1247px) {
  .header-strip__logo {
    order: 3;
    margin-right: 0;
  }
}
.header-strip__logo img {
  height: 60px;
}
.header-strip__logo:hover {
  filter: drop-shadow(0 0 10px rgba(250, 250, 250, 0.5));
}
.header-strip__phone {
  position: relative;
}
@media (max-width: 767px) {
  .header-strip__phone {
    width: calc(100% - 30px);
    margin-bottom: 10px;
  }
}
.header-strip__phone a {
  display: block;
  position: relative;
  border: solid 1px #ffffff;
  height: 60px;
  width: 60px;
  display: flex;
  margin-right: 10px;
  transition: 0.3s;
  cursor: pointer;
}
@media (max-width: 767px) {
  .header-strip__phone a {
    width: 100%;
  }
}
.header-strip__phone a:hover img {
  animation-name: phone;
}
.header-strip__phone img {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  margin: auto;
}
@media (max-width: 767px) {
  .header-strip__btn {
    width: calc(100% - 30px);
  }
}
.header-strip__btn button {
  width: 100%;
}
.header-strip__btn button::after {
  display: none;
}
.header-strip__menu-list {
  list-style-type: none;
  display: flex;
  align-items: center;
}
@media (max-width: 1247px) {
  .header-strip__menu-list {
    display: none;
  }
}
.header-strip__menu-list.open-menu {
  display: block;
  padding-left: 45px;
  min-height: 468px;
}
.header-strip__menu-list.open-menu li + li {
  margin-left: 0;
}
.header-strip__menu-list.open-menu a {
  font-size: 36px;
  font-weight: 700;
  line-height: 150%;
  text-align: left;
  margin-bottom: 15px;
  color: #131313;
  text-shadow: 1px 0 1px #808080, 0 1px 1px #808080, -1px 0 1px #808080, 0 -1px 1px #808080;
}
@media (max-width: 767px) {
  .header-strip__menu-list.open-menu a {
    font-size: 20px;
  }
}
.header-strip__menu-list a {
  color: #ffffff;
  font-size: 14px;
  text-transform: uppercase;
  position: relative;
  transition: 0.5s;
  display: block;
  font-family: "Montserrat", Arial, sans-serif;
  font-weight: 700;
}
@media (max-width: 1440px) {
  .header-strip__menu-list a {
    font-size: 12px;
  }
}
.header-strip__menu-list a:after {
  content: "";
  display: block;
  background-color: #ffffff;
  height: 1px;
  width: 0%;
  transition: 0.3s;
  transform: translateX(-50%);
  position: absolute;
  left: 50%;
}
.header-strip__menu-list a:hover:after, .header-strip__menu-list a:hover:before {
  width: 100%;
}
.header-strip__menu-list li + li {
  margin-left: 1em;
}

.burger-menu {
  margin-right: 20%;
  cursor: pointer;
  height: 20px;
  width: 30px;
  overflow: visible;
  position: relative;
  z-index: 100;
  display: none;
}
@media (max-width: 1247px) {
  .burger-menu {
    display: block;
    order: 1;
  }
}

.burger-menu span,
.burger-menu span:before,
.burger-menu span:after {
  background: #ffffff;
  display: block;
  height: 2.5px;
  border-radius: 100px;
  opacity: 1;
  position: absolute;
  transition: 0.3s ease-in-out;
}

.burger-menu span::after, .burger-menu span:before {
  content: "";
}

.burger-menu span {
  right: 0px;
  top: 10px;
  width: 27px;
}
.burger-menu span::before {
  left: 0px;
  top: -7px;
  width: 50%;
}
.burger-menu span::after {
  left: 0px;
  top: 7px;
  width: 80%;
}
.burger-menu.close span {
  transform: rotate(-45deg);
  top: 13px;
  width: 27px;
}
.burger-menu.close span::before {
  top: 0px;
  transform: rotate(90deg);
  width: 27px;
}
.burger-menu.close span::after {
  top: 0px;
  left: 0;
  transform: rotate(90deg);
  opacity: 0;
  width: 0;
}

.anim-phone {
  animation-duration: 1200ms;
  animation-timing-function: ease;
  animation-iteration-count: infinite;
}

@keyframes phone {
  0% {
    transform: scale(1) skew(15deg);
  }
  50% {
    transform: scale(1.2) skew(15deg);
  }
  100% {
    transform: scale(1) skew(15deg);
  }
}