
html,body,div,span,object,iframe,h1,h2,h3,h4,h5,h6,p,a,em,img,b,u,i,ol,ul,li,form,label,table,tbody,tfoot,thead,tr,th,td,input,textarea,button {
	margin: 0;
	 padding: 0;
	 border: none medium;
 }
 
 html, body {
	 font-size: 16px;
	 line-height: 1.5;
 }
 body {
	 font-family: $Montserrat;
		background-color: $bg;
	 &.is-hidden {
		 overflow: hidden;
	 }
	 @media ($desktop) {
		 background-color: $mobile;
	 }
 }
 html {
	 -ms-text-size-adjust: 100%;
	 -webkit-text-size-adjust: 100%;
 }
 
 *, *:focus, *:after, *:before {
	 -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
	 -moz-box-sizing: border-box;
	 box-sizing: border-box;
	 outline: none;
 }
 a, a:active {
	text-decoration: none;
 }
 img {
	 max-width: 100%;
 }
 .wrapper {
	max-width: 1820px;
	width: 100%;
	padding: 0 50px;
	margin: 0 auto;
	@media ($phoneL) {
	  padding: 0 15px;
	}
 }
 button {
	display: inline-block;
	background: none;
	outline: none;
	cursor: pointer;
	font-size: 12px;
	font-weight: 700;
 }
 .skew {
	 transform: skew(-15deg);
	 &__inner {
		 transform: skew(15deg);
		 display: block;
	 }
 }
 
 .btn {
	 background-color: $gold;
	 padding: 0 75px;
	 min-height: 60px;
	 color: $white;
	 position: relative;
	 white-space: nowrap;
	 @media ($phone) {
		 min-height: 45px;
		 padding: 0 47px;
	 }
	 &::after {
		 content: '';
		 display: block;
		 position: absolute;
		 right: -8px;
		 top: 0;
		 bottom: 0;
		 width: 4px;
		 height: 100%;
		 background-color: $white;
		 z-index: 10;
	 }
 }
 .title {
	 font-size: 42px;
	 font-weight: 700;
	 line-height: 1em;
	 text-transform: uppercase;
	 @media ($tablet) {
		 font-size: 38px;
	 }
	 @media ($phone) {
		 font-size: 20px;
	 }
 }
 .site-container {
	 position: relative;
 }
 .btn-form {
	 &:disabled {
		 cursor: default;
		 opacity: 0.5;
	 }
 }
 
 .field-error {
	 border: solid 1px red!important;
 }
 
 //----modal
 
 .modal {
	 display: none;
	 &.is-open {
		 display: block;
	 }
	 &__inner {
		 width: 50vw;
		 z-index: 200;
		 color: $white;
		 position: fixed;
		 left: 50%;
		 top: 50%;
		 transform: translate(-50%, -50%);
		 box-shadow: 0 0 10px rgba($white, 0.2);
		 border-radius: 10px;
		 padding: 40px;
		 background-color: $lighTgray;
		 @media ($phone) {
			 width: 80vw;
		 }
	 }
	 &__close {
		 position: absolute;
		 right: 10px;
		 top: 10px;
		 display: block;
		 width: 20px;
		 height: 20px;
		 cursor: pointer;
		 z-index: 121;
		 transition: .3s;
		 &:hover {
			 transform: rotate(90deg);
		 }
		 &::after {
			 content: '';
			 width: 100%;
			 height: 1px;
			 background-color: $white;
			 display: block;
			 transform: rotate(45deg);
			 position: absolute;
			 left: 0;
			 top: 9px;
		 }
		 &::before {
			 content: '';
			 width: 100%;
			 height: 1px;
			 background-color: $white;
			 display: block;
			 transform: rotate(-45deg);
			 position: absolute;
			 left: 0;
			 top: 9px;
		 }
	 }
 }
 .modal-form {
	 input {
		 display: block;
		 width: 100%;
		 padding: 10px 10px;
		 outline: none;
		 border: solid 1px transparent;
		 margin: 0 0 15px;
		 font-size: 14px;
	 }
 }
 .overlay {
	 position: fixed;
	 top: 0;
	 right: 0;
	 bottom: 0;
	 left: 0;
	 background-color: rgba(#000, 0.2);
	 z-index: 150;
	 backdrop-filter: blur(4px);
 }
 .overlay-menu {
	 position: fixed;
	 top: 100px;
	 right: 0;
	 bottom: 0;
	 left: 0;
	 background-color: $mobile;
	 z-index: 100;
 }